import React from 'react';
import { AuthContext } from '../App';
import Button from '../components/Button'
import { useHistory } from 'react-router-dom';
import axios from 'axios'

export const Login = () => {
  const history = useHistory()
  const { dispatch } = React.useContext(AuthContext);
  const initialState = {
    email: '',
    password: '',
    isSubmitting: false,
    errorMessage: null
  };

  const [data, setData] = React.useState(initialState);

  const handleInputChange = event => {
    setData({
      ...data,
      [event.target.name]: event.target.value
    });
  };

  const handleFormSubmit = event => {
    event.preventDefault();
    setData({
      ...data,
      isSubmitting: true,
      errorMessage: null
    });

    axios.post("http://10.4.20.197/api/authenticate", {
      email: data.email,
      password: data.password
    })
    .then(response => {
      dispatch({
          type: "LOGIN",
          payload: response.data
      })
      history.push('/')
    })
    .catch(error => {
        console.log(error.response);
        setData({
        ...data,
        isSubmitting: false,
        errorMessage: error.message || error.statusText
        });
    });
  };

  return (
    <div className='login-container'>
      <div className='card'>
        <form className='form-default' onSubmit={handleFormSubmit}>
          LOGIN

          <label htmlFor='email'>
            Email Address
          </label>
          <input
              type='text'
              value={data.email}
              onChange={handleInputChange}
              name='email'
            />

          <label htmlFor='password'>
            Password
          </label>
          <input
              type='password'
              value={data.password}
              onChange={handleInputChange}
              name='password'
            />

          {data.errorMessage && (
            <span className='form-error'>{data.errorMessage}</span>
          )}

          <Button className='btn btn-default' disabled={data.isSubmitting}>
            {data.isSubmitting ? (
              'Processing'
            ) : (
              'Login'
            )}
          </Button>
          <a className='link link-default' href='/register'>New User?</a>
        </form>
      </div>
    </div>
  );
};

export default Login;
