import React, { useState, useEffect } from 'react';
import axios from 'axios'
// import { AuthContext } from '../App';
import { trackPromise, usePromiseTracker } from 'react-promise-tracker'

import Button from '../components/Button'
import Search from '../components/TitleSearch'

import LoadingIndicator from '../components/LoadingIndicator'

const frequentItemList = [
  'frequentItemset1',
  'frequentItemset2',
  'frequentItemset3',
  'frequentItemset4',
  'frequentItemset5'
]

const years = [
  2010, 2011, 2012, 2013, 2014, 2015,2016, 2017, 2018, 2019, 2020
]

export const Home = () => {
  // const { state } = React.useContext(AuthContext);
  const { promiseInProgress } = usePromiseTracker()

  const [selectedItemsets, setSelectedItemsets] = useState([])

  const [fromYear, setFromYear] = useState(undefined)
  const [toYear, setToYear] = useState(undefined)

  const [authorSearch, setAuthorSearch] = useState('')
  const [topicsSearch, setTopicsSearch] = useState('')
  const [authorsSelected, selectAuthors] = useState([])
  const [topicsSelected, selectTopics] = useState([])

  const [authors, setAuthors] = useState([])
  const [topics, setTopics] = useState([])

  const [files, setFiles] = useState([])

  const handleItemsetClick = (ele) => {
    if(selectedItemsets && selectedItemsets.indexOf(ele) > -1){
      setSelectedItemsets(selectedItemsets.filter((val) => val !== ele))
    }else setSelectedItemsets([...selectedItemsets, ele])
  }

  const onSubmit = async () => {
    const response = await axios.post("http://10.4.20.197/api/query", {
      authors: authorsSelected.map((ele) => ele.authorId),
      topics: topicsSelected.map((ele) => ele.topicId),
      fromYear: fromYear,
      toYear: toYear
    })
    setFiles(response.data)
  }

  useEffect(() => {
    const fetchAuthors = async () => {
      const response = await axios.get("http://10.4.20.197/api/getAuthors")
      setAuthors(response.data)
    }
    trackPromise(fetchAuthors())
    const fetchTopics = async () => {
      const response = await axios.get("http://10.4.20.197/api/getTopics")
      setTopics(response.data)
    }
    trackPromise(fetchTopics())
  }, [])

  return (
    <div className='home'>
	{console.log(files)}
	  {promiseInProgress ? <LoadingIndicator /> : (
        <div className='flex flex-row flex-space-between'>
        <div className='flex flex-column filters'>
          <div className='flex flex-column filter'>
            <span className='filter-header'> Year </span>
            <select className='select-default' onChange={(event) => {
                setFromYear(event.target.value)
                setToYear(event.target.value)
              }}>
              <option defaultValue={true}>From</option>
              {years.map((ele, key) => {
                return <option key={key} value={ele} >{ele}</option>
              })}
            </select>
            {fromYear && 
              <select className='select-default' onChange={(event) => {
                setToYear(event.target.value)
              }}>
                <option defaultValue={true}>To</option>
                {years.map((ele, key) => {
                  return fromYear <= ele ? <option key={key} onClick={() => setFromYear(ele)}>{ele}</option> : ''
                })}
              </select>
            }
          </div>
          <div className='flex flex-column filter'>
            <span className='filter-header'> Search Authors </span>
            <input
              className='input-default'
              type='text'
              name='author'
              onChange={(event) => setAuthorSearch(event.target.value)}
              autoComplete='off'
              value={authorSearch}
            />
            <Search 
              listToBeSearched={authors} 
              text={authorSearch} 
              clear={setAuthorSearch} 
              setItems={selectAuthors} 
              itemsSelected={authorsSelected}/>
          </div>
          <div className='flex flex-column filter'>
            <span className='filter-header'> Authors Selected </span>
            {authorsSelected.length === 0 ? 'None': (
              <div>
                Click to deselect
              {authorsSelected.map((ele, key) => {
                return <div className='author-selections' 
                            key={key}
                            onClick={() => selectAuthors(authorsSelected.filter((author) => author !== ele))}> {ele.authorName} </div>
              })}
              </div>
            )}
          </div>
        </div>
        <div className='flex flex-column'>
          RESULTS
            <Button className='btn btn-default' onClick={onSubmit}>
              Show
            </Button>
          {files.map((ele, key) => {
            return (
              <p key={key}>{ele}</p>
            )
          })}
        </div>
        <div className='flex flex-column filters'>
          <div className='flex flex-column filter'>
            <span className='filter-header'> Search Topics </span>
            <input
              className='input-default'
              type='text'
              name='author'
              onChange={(event) => setTopicsSearch(event.target.value)}
              autoComplete='off'
              value={topicsSearch}
            />
            <Search 
              listToBeSearched={topics} 
              text={topicsSearch} 
              clear={setTopicsSearch} 
              setItems={selectTopics} 
              itemsSelected={topicsSelected}/>
          </div>
          <div className='flex flex-column filter'>
            <span className='filter-header'> Topics Selected </span>
            {topicsSelected.length === 0 ? 'None': (
              <div>
                Click to deselect
              {topicsSelected.map((ele, key) => {
                return <div className='author-selections' 
                            key={key}
                            onClick={() => selectTopics(topicsSelected.filter((topic) => topic !== ele))}> {ele.topicName} </div>
               })}
              </div>
            )}
          </div>
          <div className='flex flex-column filter'>
            <span className='filter-header'> Frequent Itemsets </span>
            {frequentItemList.map((ele, key) => {
              return (
                <div className='flex flex-row checkbox-wrapper' key={key}>
                  <input className='checkbox' 
                        type='checkbox' 
                        name='frequentItemsets' 
                        value={ele}
                        onChange={() => handleItemsetClick(ele)}
                         />
                  <label htmlFor={ele}>{ele}</label>
                </div>
              )
            })}
          </div>
        </div>
      </div>
      )}
    </div>
  );
};

export default Home;
